import request from '@/utils/request'
import qs from 'qs'

// 按条件分页查询资源
export const getResourcePages = data => {
  return request({
    method: 'post',
    url: '/boss/resource/getResourcePages',
    data
  })
}

// 查询资源分类列表
export const getResourceCategories = () => {
  return request({
    method: 'get',
    url: '/boss/resource/category/getAll'
  })
}

// 保存或更新资源
export const createOrUpdateResource = data => {
  return request({
    method: 'post',
    url: '/boss/resource/saveOrUpdate',
    data
  })
}

// 删除资源
export const deleteResource = id => {
  return request({
    method: 'delete',
    url: `/boss/resource/${id}`
  })
}

// 获取资源
export const getResourceById = id => {
  return request({
    method: 'get',
    url: `/boss/resource/${id}`
  })
}

// 获取资源分类列表
export const getAllResourceCategory = resourceId => {
  return request({
    method: 'get',
    url: '/boss/resource/category/getAll',
    data: qs.stringify(resourceId)
  })
}

// 保存或更新资源分类
export const createOrUpdateCategory = data => {
  return request({
    method: 'post',
    url: '/boss/resource/category/saveOrderUpdate',
    data
  })
}

// 删除资源分类
export const deleteResourceCategory = id => {
  return request({
    method: 'delete',
    url: `/boss/resource/category/${id}`
  })
}

// 获取所有资源
export const getAllResources = () => {
  return request({
    method: 'get',
    url: '/boss/resource/getAll'
  })
}

// 获取角色拥有的资源
export const getRoleResources = data => {
  return request({
    method: 'get',
    url: '/boss/resource/getRoleResources',
    data
  })
}

// 给角色分配资源
export const allocateRoleResources = data => {
  return request({
    method: 'post',
    url: '/boss/resource/allocateRoleResources',
    data
  })
}
