<template>
  <div class="resource-list">
    <!-- 资源查询表单 -->
    <el-card
      shadow="never" >
      <el-form
        ref="resource-form"
        :inline="true"
        :model="resourceQuery"
        class="demo-form-inline">
        <el-form-item
          label="资源名称："
          prop="name">
          <el-input
            v-model="resourceQuery.name"
            placeholder="资源名称"
            size="medium"
            clearable ></el-input>
        </el-form-item>
        <el-form-item
          label="资源路径："
          prop="url" >
          <el-input
            v-model="resourceQuery.url"
            placeholder="资源路径"
            size="medium"
            clearable ></el-input>
        </el-form-item>
        <el-form-item
          label="资源分类："
          prop="categoryId" >
          <el-select
            v-model="resourceQuery.categoryId"
            placeholder="资源分类"
            size="medium"
            clearable >
            <el-option
              v-for="item in resourceCategories"
              :key="item.id"
              :label="item.name"
              :value="item.id"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item
          style="float: right; margin-right: 20px;">
          <el-button
            size="small"
            @click="onResourceQueryReset" >重置</el-button>
          <el-button
            size="small"
            type="primary"
            @click="onResourceQuerySubmit"
            :disabled="isLoading" >查询搜索</el-button>
        </el-form-item>
      </el-form>
    </el-card>
    <!-- 资源操作 -->
    <el-card
      shadow="never" >
      <el-button
        size="mini"
        @click="handleAddResource" >添加</el-button>
      <el-button
        size="mini"
        @click="handleResCategory" >资源分类</el-button>
    </el-card>
    <!-- 资源数据列表 -->
    <el-table
      :data="resources"
      v-loading="isLoading"
      style="width: 100%"
      border >
      <el-table-column
        type="index"
        label="编号"
        align="center"
        width="80" />
      <el-table-column
        prop="name"
        label="资源名称"
        align="center" />
      <el-table-column
        prop="url"
        label="资源路径"
        align="center" />
      <el-table-column
        prop="description"
        label="描述"
        align="center" />
      <el-table-column
        label="添加时间"
        align="center" >
        <template
          slot-scope="scope">
          {{ scope.row.createdTime | dateFormat }}
        </template>
        </el-table-column>
      <el-table-column
        label="操作"
        align="center" >
        <template
          slot-scope="scope">
          <el-button
            type="text"
            @click="handleEditResource(scope.row)" >编辑</el-button>
          <el-button
            type="text"
            @click="handleDeleteResource(scope.row)" >删除</el-button>
        </template>
      </el-table-column>
    </el-table>
    <!-- 分页栏 -->
    <el-pagination
      @size-change="handlePageSizeChange"
      @current-change="handleCurrentPageChange"
      :page-sizes="[10, 15, 25]"
      :total="resourceTotalCount"
      :page-size="resourceQuery.size"
      :current-page="resourceQuery.current"
      layout="total, sizes, prev, pager, next, jumper"
      :disabled="isLoading" ></el-pagination>
    <!-- 添加/编辑资源对话框 -->
    <el-dialog
      v-if="resDialogVisible"
      :title="`${isEditRes ? '编辑' : '添加'}资源`"
      :visible.sync="resDialogVisible"
      width="30%">
      <resource-create-or-edit
        :is-edit="isEditRes"
        :res-id="resId"
        @success="handleResSuccess"
        @cancel="handleResCancel"
      ></resource-create-or-edit>
    </el-dialog>
  </div>
</template>

<script>
import {
  getResourcePages,
  getResourceCategories,
  deleteResource
} from '@/services/resource'
import {
  toStandardTimeFormat
} from '@/utils/format'
import ResourceCreateOrEdit from './create-or-edit'

export default {
  name: 'ResourceList',
  components: {
    ResourceCreateOrEdit
  },
  data () {
    return {
      resourceQuery: {
        current: 1,
        size: 10,
        name: '',
        url: '',
        categoryId: ''
      },
      resourceTotalCount: 0,
      resourceCategories: [],
      resources: [],
      isLoading: false,
      resDialogVisible: false,
      isEditRes: false,
      resId: ''
    }
  },
  created () {
    this.loadResoruces()
    this.loadResourceCategories()
  },
  methods: {
    async loadResoruces () {
      this.isLoading = true
      const { data } = await getResourcePages(this.resourceQuery)
      if (data.code === '000000') {
        this.resources = data.data.records
        this.resourceTotalCount = data.data.total
      }
      this.isLoading = false
    },
    async loadResourceCategories () {
      this.isLoading = true
      const { data } = await getResourceCategories()
      if (data.code === '000000') {
        this.resourceCategories = data.data
      }
      this.isLoading = false
    },
    handlePageSizeChange (pageSize) {
      this.resourceQuery.size = pageSize
      this.resourceQuery.current = 1
      this.loadResoruces()
    },
    handleCurrentPageChange (current) {
      this.resourceQuery.current = current
      this.loadResoruces()
    },
    handleAddResource () {
      this.resDialogVisible = true
      this.isEditRes = false
      this.resId = ''
    },
    handleEditResource (res) {
      this.resDialogVisible = true
      this.isEditRes = true
      this.resId = res.id
    },
    handleDeleteResource (res) {
      this.$confirm('确认删除吗？', '删除提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(async () => {
        const { data } = await deleteResource(res.id)
        if (data.code === '000000') {
          this.$message.success('删除成功')
          this.loadResoruces()
        }
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '已取消删除'
        })
      })
    },
    handleResCategory () {
      this.$router.push({
        name: 'resource-category'
      })
    },
    handleResSuccess () {
      this.resDialogVisible = false
      this.loadResoruces()
    },
    handleResCancel () {
      this.resDialogVisible = false
    },
    onResourceQuerySubmit () {
      this.resourceQuery.current = 1
      this.loadResoruces()
    },
    onResourceQueryReset () {
      this.$refs['resource-form'].resetFields()
    }
  },
  filters: {
    dateFormat (date) {
      return toStandardTimeFormat(date)
    }
  }
}
</script>

<style lang="scss" scoped>
.resource-list {
  .el-pagination {
    margin-top: 20px;
  }
}
</style>
