<template>
  <div class="resource-create-or-edit">
    <el-form
      label-position="top">
      <el-form-item
        label="资源名称：">
        <el-input
          v-model="resource.name"></el-input>
      </el-form-item>
      <el-form-item
        label="资源路径：">
        <el-input
          v-model="resource.url"></el-input>
      </el-form-item>
      <el-form-item
        label="资源分类：">
        <el-select
          v-model="resource.categoryId"
          placeholder="资源分类"
          style="width: 100%;" >
          <el-option
            v-for="item in resourceCategories"
            :key="item.id"
            :label="item.name"
            :value="item.id"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item
        label="描述：">
        <el-input
          v-model="resource.description"
          type="textarea"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button
          size="small"
          @click="hanldeResDialogCancal">取消</el-button>
        <el-button
          size="small"
          type="primary"
          @click="hanldeResDialogSubmit">确定</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
import {
  getResourceCategories,
  getResourceById,
  createOrUpdateResource
} from '@/services/resource'

export default {
  name: 'ResourceCreateOrEdit',
  props: {
    isEdit: {
      type: Boolean
    },
    resId: {
      type: [Number, String]
    }
  },
  data () {
    return {
      resource: {},
      resourceCategories: []
    }
  },
  created () {
    this.loadResourceCategories()
    if (this.isEdit) {
      this.loadResource()
    }
  },
  methods: {
    async loadResourceCategories () {
      const { data } = await getResourceCategories()
      if (data.code === '000000') {
        this.resourceCategories = data.data
      }
    },
    async loadResource () {
      const { data } = await getResourceById(this.resId)
      this.resource = data.data
    },
    async hanldeResDialogSubmit () {
      const { data } = await createOrUpdateResource(this.resource)
      if (data.code === '000000') {
        this.$message.success('提交成功')
        this.$emit('success')
        this.resource = {}
      }
    },
    hanldeResDialogCancal () {
      this.$emit('cancel')
      this.resource = {}
    }
  }
}
</script>

